<template>
  <div class="row bg-white max-height-vertical box-overflow">
    <full-span-slide>
      <h1 class="upcase">{{title}}</h1>
      <p v-for="paragraph in content.paragraphs" v-bind:key="paragraph.index">{{paragraph}}</p>

      <div v-if="content.text_box">
        <textarea class="form-text-area p" v-bind:placeholder="content.text_box" v-on:keydown="checkForEnter"></textarea>
      </div>

    </full-span-slide>
  </div>
</template>

<script>
  import full_span_slide from '../../components/organisms/full_span_slide'

  import 'nodelist-foreach-polyfill'; // ie11

  export default {
    props: ['title', 'content', 'active'],
    methods: {
      checkForEnter(e) {
        if (e.keyCode === 13) {
          setTimeout(() => {
            // window resize trigger
            window.dispatchEvent(new Event('resize'));
            this.$store.commit('change_page', 1)
          }, 250)
        }
      }
    },
    components: {
      'full-span-slide': full_span_slide
    }
  }

</script>

<style scoped lang="scss">
  @import '../../styles/variables';

  .form-text-area {
    position: relative;
    margin: auto;
    box-sizing: border-box;
    height: 120px;
    width: 100%;
    padding: 12px;
    margin-top: 12px;
    border: 1px solid $light-blue;
    border-radius: 0;
    -webkit-appearance: none;
    outline: none;
    resize: none;
    transition: border $transition-1;
    &:focus {
      border: 1px solid $blue;
    }
  }
</style>




