<template>

  <div container-card="flip-card" class="relative offset-1-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs share-flip-outer no-border outer-test" v-bind:class="cardSizeClass" :style="ratio_styles">

    <div class="flip-container">

      <div class="flipper crosshair-rollover-trigger" v-on:click="flip">

        <!-- front outer -->
        <div class="front ind-2 border-blue-hover" front-card :class="flipped">
          
          <!-- inner front content -->
          <div class="front-inner">

            <div class="svg-icon-background"><component :is="icon"></component></div>
            <div class="subheader upcase">{{title}}</div>

          </div>

        </div>

        <!-- back outer -->
        <div class="back border-blue-hover" :class="flipped">

          <div class="scroll-outer maintain-outer">

            <!-- inner back content -->
            <div class="back-inner">
              
              <div class="subheader upcase"> 
                <div class="svg-icon-background svg-icon-background-small"><component :is="icon"></component></div> <span>{{title}}</span>
              </div>

              <p><slot></slot></p>

            </div>

          </div>

        </div>

        

      </div>

      <crosshair-background></crosshair-background>
      

    </div>

  </div>
</template>

<script>
  import crosshair_background from '../atoms/crosshair_background'
  import svg from '../svg'

  import {ratio_height, ratio_styles} from '../../utils/ratio_height'

  export default {
    props: ['title', 'icon', 'cardSize'],
    components: {
      'crosshair-background': crosshair_background,
      ...svg
    },
    data () {
      return {
        ratio_styles,
        flipped: null
      }
    },
    methods: {
      get_ratio (w) {
        if (w.innerWidth < 1000) {
          return 2/3
        }
        return 5/4
      },
      flip () {
        if(!this.flipped) {
          this.flipped = 'flipped'
          this.$emit('flip', 1)
        } else {
          this.flipped = null
          this.$emit('flip', -1)
        }
      }
    },
    computed: {
      cardSizeClass () {
        if (this.cardSize > 3 ) {
          return 'span-3-lg'
        } else {
          return 'span-4-lg'
        }
      }
    },
    mounted () {
      ratio_height(this, '[container-card="flip-card"]', this.get_ratio(window))
      window.addEventListener('resize', () => {
        ratio_height(this, '[container-card="flip-card"]', this.get_ratio(window))
      })
    }
  }
</script>

<style scoped lang="scss">

  @import '../../styles/variables';

  //- col with rollover and 3d rotate -//
  // (see https://davidwalsh.name/css-flip#ieflip)

  // perspective container
  .flip-container {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    perspective: 1200px;
    transform-style: preserve-3d;
  }

  .front {
    position: absolute;
    // box-sizing: border-box;
    display: flex;
    top: 0;
    left: 0;
    transform: rotateY(0deg);
  }

  .back {
    position: absolute;
    // box-sizing: border-box;
    top: 0;
    left: 0;
    overflow: hidden;
    display: flex;
    // width: 100%;
    // height: 100%;
    transform: rotateY(-180deg);
  }

  .front, .flipper, .back {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
  }
  
  /* flip speed goes here */
  .flipper {
    position: relative;
    cursor: pointer;
    opacity: 0.75;
    transition: 0.75s;
    transform-style: preserve-3d;
    .crosshair-container {
      opacity: 0;
    }
  }

  /* hide back of pane during swap */
  .front, .back {
    box-sizing: border-box;
    border: 1px solid transparent;
    background-color: $white-solid;
    backface-visibility: hidden;
    transition: 0.75s;
    transform-style: preserve-3d;
  }

  .back.flipped {
    transform: rotateY(0deg);
    // max-height: 100%;
  }

  .front.flipped {
    transform: rotateY(180deg);
  }

  // inner content

  .front-inner, .back-inner {
    position: relative;
    margin: auto;
    box-sizing: border-box;
  }

  .front-inner {
    padding: 24px;
    text-align: center;
  }

  .back-inner {
    padding: 24px;
  }

  .svg-icon-background {
    position: relative;
    margin-bottom: 24px;
  }

  .svg-icon-background-small {
    display: inline-block;
    margin-right: 6px;
    transform: translateY(4px);
  }

  .md-margin-top {
    @media screen and (max-width: $md-max) {
      margin-top: 24px;
    }
  }

</style>

<style lang="scss">
  .svg-icon-background-small {
    svg {
      width: 24px;
      height: 24px;
    }
  }
</style>


