<template>
  <div>

    <div class="gutter-horizontal">

      <div v-for="(page, index) in pages" v-bind:key="index" class="slide" v-bind:class="slide_active(index).class">
        <component v-bind:is="page.template" v-bind:content="page.content" v-bind:title="page.title" v-bind:active="slide_active(index).bool">
          {{page.content}}
        </component>
      </div>

    </div>

    <navbar></navbar>
    
    <notification v-bind:state="notification_state" v-bind:captionState="caption_state"></notification>

  </div>
</template>

<style scoped lang="scss">
  @import '../styles/variables';

  .slide {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    transition: transform 0.9s ease-in-out, opacity 0.4s ease-in-out;
    &.page-inactive {
      transform: translateX(-200%);
      opacity: 0;
      @media screen and (max-width: $md-max) {
        position: fixed;
        width: calc(100% - 80px - 80px);
        margin-left: 80px;
      }
      @media screen and (max-width: $sm-max) {
        width: 100%;
      }
    }
    @media screen and (max-width: $md-max) {
      position: relative;
      height: auto;
      width: auto;
    }
  }
</style>

<script>
  import navbar from '../components/organisms/navbar'
  import notification from '../components/organisms/notification'
  import pages from './pages'

  import contentful_init from '../contentful'
  
  export default {
    computed: {
      pages () {
        return this.$store.state.pages
      },
      notification_state () {
        return this.$store.state.notification_state
      },
      caption_state  () {
        return this.$store.state.checkbox
      }
    },
    methods: {
      slide_active (page) {
        let current_page = this.$store.state.page
        if (current_page !== page) {
          return {
            class: 'page-inactive',
            bool: false
          }
        } else {
          return {
            class: '',
            bool: true
          }
        }
      },
    },
    components: {
      navbar,
      notification,
      ...pages
    },
    mounted () {
      contentful_init(this, 'set_pages')
    }
  }
</script>



