<template>
  <div class="span-8-lg radio-span">
    <span><radio :correct="correct" :caption="caption"></radio></span>
    <p v-if="is_strong">
      <strong>
        <slot></slot>
      </strong>
    </p>
    <p v-else>
      <slot></slot>
    </p>
  </div>
</template>

<script>
  import radio from '../atoms/radio'
  export default {
    props: ['strong', 'correct', 'caption'],
    components: {
      'radio': radio
    },
    computed: {
      is_strong () {  return this.strong /* === true || this.strong === 'true' */ }
    },
  }
</script>

<style scoped lang="scss">
  .radio-span {
    cursor: default;
    display: flex;
    align-items: center;
  }
</style>


